import { QuestionMarkIcon } from '@/components/Icons';

export default function NotFound() {
  return (
    <div className="min-h-screen flex items-center justify-center bg-slate-50" style={{
      backgroundImage: `
        radial-gradient(circle at 20% 80%, rgba(120, 119, 198, 0.1) 0%, transparent 50%),
        radial-gradient(circle at 80% 20%, rgba(255, 119, 198, 0.1) 0%, transparent 50%),
        radial-gradient(circle at 40% 40%, rgba(120, 219, 255, 0.1) 0%, transparent 50%)
      `
    }}>
      <div className="text-center">
        <div className="bg-white/70 backdrop-blur-lg rounded-2xl shadow-xl shadow-slate-300/20 border border-white/20 p-12 max-w-md mx-auto">
          <QuestionMarkIcon className="mx-auto mb-4 text-slate-400" size={64} />
          <h1 className="text-2xl font-bold text-primary mb-4 drop-shadow-sm">Program Not Found</h1>
          <p className="text-slate-600">The program you&apos;re looking for doesn&apos;t exist or has been removed.</p>
        </div>
      </div>
    </div>
  );
}